function sliderMain() {
    var swiper = new Swiper('.main .swiper', {
        slidesPerView: '1',
        effect: "fade",
        navigation: {
            nextEl: '.main .swiper-button-next',
            prevEl: '.main .swiper-button-prev',
        },
        pagination: {
            el: ".main .swiper-pagination",
            clickable: true,
        },
    })
}

function sliderAbout() {
    var swiper = new Swiper('.about .swiper', {
        slidesPerView: '1',
        effect: "fade",
        navigation: {
            nextEl: '.about .swiper-button-next',
            prevEl: '.about .swiper-button-prev',
        },
        pagination: {
            el: ".about .swiper-pagination",
            clickable: true,
        },
    })
}

function sliderCertificates() {
    var swiper = new Swiper('.certificates .swiper', {
        slidesPerView: '4',
        spaceBetween: 40,
        navigation: {
            nextEl: '.certificates .swiper-button-next',
            prevEl: '.certificates .swiper-button-prev',
        },
        pagination: {
            el: ".certificates .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            1301: {
                slidesPerView: 4,
                spaceBetween: 40,
            },
            1161: {
                slidesPerView: 4,
                spaceBetween: 30,
            },
            961: {
                slidesPerView: 4,
                spaceBetween: 24,
            },
            741: {
                slidesPerView: 3,
                spaceBetween: 24,
            },
            451: {
                slidesPerView: 2,
                spaceBetween: 16,
            },
            320: {
                slidesPerView: 1,
                spaceBetween: 16,
            },
        }
    })
}


$(document).ready(function() {
    sliderMain();
    sliderAbout();
    sliderCertificates()

    // Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header-top').toggleClass('header-top--active')
        $('body').toggleClass('fixed-body')
    })

    // Services - menu
    $(".header-top__item").hover(onIn, onOut);
    $(".header-top__item .header-top__item-show").click(function() {
        if ($(this).parents(".header-top__item").hasClass("header-top__item--active")) {
            $(this).parents(".header-top__item").removeClass("header-top__item--active")
        } else {
            $(this).parents(".header-top__item").addClass("header-top__item--active")
        }
    })

})

function onIn() {
    if (window.innerWidth > 740) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("header-top__item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 740) {
        $(this).removeClass("header-top__item--active")
    }
}